@use '../helpers' as h;

html {
  font-size: 100%;
}

body {
  font-family: h.$font-family;
  font-weight: 500;
  font-size: h.rem(16);
  color: h.$primary-color;
  background-color: #F5F5F5;
}

body.active-left{
  .l-header, .l-main, .new-collection-block, .blackline{
    padding-left: 70px;
    transition: all ease 0.2s;

  }
}

body.active-right{
  .l-header, .l-main, .new-collection-block, .blackline .norm{
    padding-right: 70px;
    transition: all ease 0.2s;

  }
}

/* width  body.classList.add("active-left")  ;   */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 0px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(25, 25, 25);
  border-radius: 0px;
}

.content{
  max-width: 1190px;
  margin: 0 auto 50px;
  padding: 0px 15px;
}

.overflow-block{
  background-color: rgba(1,1,1,.4);
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 0; 
  opacity: 0;
  pointer-events: none;
  transition: all ease-in-out 0.1s;
  z-index: 10;
}

p, h2, h3, h4 {
  word-wrap: break-word;
}

